import cn from 'classnames';
import PropTypes from 'prop-types';
import React from 'react';
import { IoIosCheckmark, IoIosWarning } from 'react-icons/io';

import Input from '../Input';

import styles from './styles.scss';

export default function InlineEdit({
    tag: Tag = Input,
    id,
    label,
    error,
    indicatorPosition = 'start',
    successfully,
    autoComplete,
    className,
    readOnly,
    ...rest
}) {
    const indicator = (error || successfully) && (
        <div className={cn(styles.icon, styles.success, styles[indicatorPosition])}>
            {error && <IoIosWarning />}
            {successfully && <IoIosCheckmark />}
        </div>
    );

    return (
        <div className={cn(styles.root, readOnly && styles.readOnly, className)}>
            {label && (
                <label htmlFor={id} className={styles.label}>
                    {label}
                </label>
            )}
            <div className={styles.inputContainer}>
                <Tag
                    id={id}
                    className={styles.input}
                    size="normal"
                    type="text"
                    autoComplete={autoComplete === 'off' ? Math.random() * 10 + '_autocomplete' : autoComplete}
                    readOnly={readOnly}
                    {...rest}
                />
                {indicator}
            </div>
        </div>
    );
}

InlineEdit.propTypes = {
    id: PropTypes.string,
    label: PropTypes.string,
    tag: PropTypes.oneOfType([PropTypes.string, PropTypes.func]),
    readOnly: PropTypes.bool,
    error: PropTypes.any,
    onFocus: PropTypes.func,
    onBlur: PropTypes.func,
    className: PropTypes.string,
    indicatorPosition: PropTypes.oneOf(['start', 'end']),
    successfully: PropTypes.bool,
    autoComplete: PropTypes.oneOf(['on', 'off']),
};
