import { useState } from 'react';
import { useHistory } from 'react-router';

import { useIntegration } from '../useIntegration';
import { useActions } from '@mssgme/hooks';

export const ERROR_MESSAGES_MAP = {
    'Not Found': 'telegramIntegration.errors.not_found',
    Unauthorized: 'telegramIntegration.errors.unauthorized',
    'errors.create_integration.telegram.not_a_token': 'telegramIntegration.errors.wrong_token',
};

export const useTelegramIntegration = ({ request }) => {
    const { api } = useActions();
    const { t, rootIntegrationsPath } = useIntegration();
    const history = useHistory();
    const [token, setToken] = useState('');

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            await request(api.integrations.create({ payload: { type: 'telegram', token } }));
            setToken('');
            history.push(rootIntegrationsPath);
        } catch (err) {
            // errors handled by request()
        }
    };

    return {
        handleSubmit,
        t,
        token,
        setToken,
    };
};
