import React from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import { useBrandingUrl, useLanguage } from '@mssgme/hooks';
import { LinkBlank, useExperiment } from '@mssgme/ui';
import logoSrc from '../../../../../img/logo-white.svg';

import styles from './BrandingTopPane.scss';

export function BrandingTopPane({ className, animated = false }) {
    const brandingUrl = useBrandingUrl();
    const lang = useLanguage();
    const expId = useExperiment({ id: 'branding' });
    const buttonText = lang === 'ru' ? 'Создать страницу' : 'Make your page';

    return (
        <div className={classNames(styles.root, className, styles.slideDown, animated ? styles.animated : null)}>
            <LinkBlank
                id="legacy-card-branding-logo"
                data-experiment={expId}
                href={brandingUrl}
                className={styles.logo}
            >
                <img src={logoSrc} alt="mssg.me logo" />
            </LinkBlank>
            <LinkBlank
                id="legacy-card-branding-link"
                data-experiment={expId}
                href={brandingUrl}
                className={styles.button}
            >
                {buttonText}
            </LinkBlank>
        </div>
    );
}

BrandingTopPane.propTypes = {
    className: PropTypes.string,
    animated: PropTypes.bool,
};
