import cn from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';

import { SelectSimple, FormErrors, Spinner, FormField } from '@mssgme/ui';
import { withApi } from '../withApi';
import { useMessengerIntegration } from './useMessengerIntegration';

import styles from '../commonStyles.scss';

function MessengerIntegrationNew({ errors: apiErrors, isFetching, request }) {
    const {
        t,
        fbApi,
        plugError,
        handleSubmit,
        isLoggedIn,
        handleLoginClick,
        isLoaded,
        username,
        selectedPage,
        options,
        handleChange,
        errors,
    } = useMessengerIntegration({ request });

    if (!fbApi) {
        return plugError && <FormErrors error={t(plugError)} />;
    }

    return (
        <form onSubmit={handleSubmit} className={styles.rootContainer}>
            {!isLoggedIn && (
                <>
                    <p>{t('messengerIntegration.connect_facebook')}</p>
                    <div className={styles.buttonContainer}>
                        <button className={cn(styles.submitButton, styles.active)} onClick={handleLoginClick}>
                            {t('messengerIntegration.sign_in_fb')}
                        </button>
                    </div>
                </>
            )}
            {isLoaded && (
                <>
                    <p style={{ marginBottom: 10 }}>
                        {t('messengerIntegration.signed_in_as')}&nbsp;<strong>{username}</strong>
                    </p>
                    <p style={{ marginBottom: 20 }}>{t('messengerIntegration.choose_page')}</p>
                    <FormField
                        component={SelectSimple}
                        inputClassName={styles.select}
                        placeholder={t('messengerIntegration.select_page')}
                        value={(selectedPage && selectedPage.id) || ''}
                        options={options}
                        onChange={handleChange}
                        errors={errors.selectedPage}
                    />
                    <div className={styles.buttonContainer}>
                        <button
                            type="submit"
                            className={cn(styles.submitButton, {
                                [styles.active]: !!selectedPage,
                                [styles.isFetching]: isFetching,
                            })}
                        >
                            {isFetching ? (
                                <Spinner size="16" color="white" className={styles.spinner} />
                            ) : (
                                t('messengerIntegration.connect')
                            )}
                        </button>
                    </div>
                </>
            )}
        </form>
    );
}

MessengerIntegrationNew.propTypes = {
    isFetching: PropTypes.bool.isRequired,
    errors: PropTypes.object.isRequired,
    request: PropTypes.func.isRequired,
};

export default withApi(MessengerIntegrationNew, 'errorsList.create_integration');
