import React from 'react';
import { render } from 'react-dom';

import { trans } from '@mssgme/shared';
import App from './components/Userpage';

trans.extend(window.LOCALES);

const data = window.INITIAL_DATA;
const basename = window.BASENAME;
const { lang } = window.document.documentElement;

setTimeout(() => {
    render(<App data={data} lang={lang} basename={basename} />, document.getElementById('root'));
}, 50);
