import cn from 'classnames';
import React from 'react';
import PropTypes from 'prop-types';

import { Spinner } from '@mssgme/ui';
import { withApi } from '../withApi';
import { ERROR_MESSAGES_MAP, useTelegramIntegration } from './useTelegramIntegration';

import styles from '../commonStyles.scss';

function TelegramIntegration({ isFetching, errors, request }) {
    const { handleSubmit, t, token, setToken } = useTelegramIntegration({ request });

    return (
        <form onSubmit={handleSubmit} className={styles.rootContainer}>
            <ol className={styles.list}>
                <li
                    dangerouslySetInnerHTML={{
                        __html: t('telegramIntegration.how_to.step1'),
                    }}
                />
                <li
                    dangerouslySetInnerHTML={{
                        __html: t('telegramIntegration.how_to.step2'),
                    }}
                />
                <li
                    dangerouslySetInnerHTML={{
                        __html: t('telegramIntegration.how_to.step3'),
                    }}
                />
                <li
                    dangerouslySetInnerHTML={{
                        __html: t('telegramIntegration.how_to.step4'),
                    }}
                />
                <li>{t('telegramIntegration.how_to.step5')}</li>
                <li
                    dangerouslySetInnerHTML={{
                        __html: t('telegramIntegration.how_to.step6'),
                    }}
                />
            </ol>
            <input
                type="text"
                placeholder={t('telegramIntegration.placeholder')}
                value={token}
                onChange={(e) => setToken(e.target.value)}
                disabled={isFetching}
                className={styles.input}
            />
            {errors.token && <span className={styles.error}>{t(errors.token)}</span>}
            <div className={styles.buttonContainer}>
                <button
                    type="submit"
                    className={cn(styles.submitButton, { [styles.active]: !!token, [styles.isFetching]: isFetching })}
                >
                    {isFetching ? (
                        <Spinner size="16" color="white" className={styles.spinner} />
                    ) : (
                        t('telegramIntegration.connect')
                    )}
                </button>
            </div>
        </form>
    );
}

TelegramIntegration.propTypes = {
    isFetching: PropTypes.bool.isRequired,
    errors: PropTypes.object.isRequired,
    request: PropTypes.func.isRequired,
};

export default withApi(TelegramIntegration, 'errorsList.create_integration', (message) => ERROR_MESSAGES_MAP[message]);
