import React from 'react';
import ReactDOM from 'react-dom';
import PropTypes from 'prop-types';

import { Notification } from '@mssgme/ui';
import styles from './styles.scss';

export class CardReviewNotificationModal extends React.Component {
    el = document.createElement('div');

    static propTypes = {
        closeModalFn: PropTypes.func.isRequired,
        message: PropTypes.string.isRequired,
        btnStyle: PropTypes.string.isRequired,
    };

    componentDidMount() {
        const modalRoot = document.getElementById('cardRoot');
        modalRoot.appendChild(this.el);
    }

    componentWillUnmount() {
        const modalRoot = document.getElementById('cardRoot');
        modalRoot.removeChild(this.el);
    }

    render() {
        const { closeModalFn, message, btnStyle } = this.props;

        return ReactDOM.createPortal(
            <div className={styles.root}>
                <div className={styles.content}>
                    <Notification notifyStyle={btnStyle} showClose closeDelay={3000} onClose={closeModalFn}>
                        {message}
                    </Notification>
                </div>
            </div>,
            this.el
        );
    }
}
