import React from 'react';
import { useTranslation } from 'react-i18next';

import { TextareaField } from '@mssgme/ui';
import { FieldTip } from '@mssgme/app/components/UI';
import CardEditForm from '../CardEditForm';

export default function DescriptionEdit() {
    const { t } = useTranslation('cards');

    return (
        <CardEditForm title={t('DescriptionEdit.title')}>
            {() => (
                <>
                    <TextareaField
                        name="profile.description"
                        placeholder={t('profileEdit.placeholder.description')}
                        maxLength={400}
                        maxRows={2}
                    />
                    <FieldTip>{t('FieldTip.description')}</FieldTip>
                </>
            )}
        </CardEditForm>
    );
}
